import React from "react";


/*****************************************************/
//
class InfoShow extends React.Component {
	render(){
		return(
			<p className={"info"}>
				<span>{this.props.title} : </span>
				<span className={"yellow"}>{this.props.info}</span>
			</p>
		);
	}
}

/*****************************************************/
//
class PaycheckMenu extends React.Component {
	render(){
		return(
			<p className={"info"}>
				<a className={"paycheckMenu"} href={this.props.url} target={"_blank"}>{this.props.title}</a>
			</p>
		);
	}
}

/*****************************************************/
//
class TopInfoPrint extends React.Component {
	render(){
		return(
			<div>
				<InfoShow title={""} info={shopInfo.smsBalance} />
				<InfoShow title={""} info={shopInfo.edmBalance} />
				<PaycheckMenu title={""} url={shopInfo.paycheckUrl} />
				<p className={"info"}>
					{shopInfo.systemModel}
				</p>
				<InfoShow title={""} info={shopInfo.deadline} />
				<PaycheckMenu title={""} url={shopInfo.crmPaycheckUrl} />
			</div>
		);
	}
}

/*****************************************************/
module.exports = TopInfoPrint;


